@import "../../global.scss";

.portfolio {
  padding: 11px;
  overflow: hidden;

  .head {
    padding: 20px;
    text-align: center;
    h2 {
      font-size: 45px;
      margin: 20px;
    }
  }
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    .card {
      border: 3px solid white;
      box-shadow: 1px 1px 1px #999;
      max-width: 600px;
      flex: 1 370px;
      border-radius: 15px;
      overflow: hidden;
      transition: 0.1s;
      .img {
        img {
          width: 100%;
        }
      }

      &:hover {
        transform: scale(1.04);
      }
      .title {
        padding: 10px;
        font-size: 25px;
        font-weight: bold;
        margin:20px 10px;
      }
      .button {
        display: flex;
        padding: 15px;
        gap: 25px;
        button {
          border: none;
          box-shadow: 0px 0px 7px 0px #666;
          padding: 3px 19px;
          border-radius: 20px;
          font-size: 18px;
          cursor: pointer;
          color: inherit;
          font-weight: 600;
          background-color: inherit;
          transition: 0.3s;
          &:hover {
            transform: scale(1.1);
          }
        }
      }
    }
  }
}

.list-li {
  ul {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    list-style: none;
    gap: 10px;
    padding-bottom: 15px;
    margin-bottom: 30px;
    li {
      cursor: pointer;

      transition: 0.3s;
      border-radius: 20px;
      font-size: 20px;
      padding: 10px 20px;
      font-weight: 700;
      &:hover {
        transform: scale(1.1);
      }
      &.active {
        background-color: #15023a;
        color: white;
      }
    }
  }
}

.dark .portfolio .container .card{
  img{
    opacity: 0.6;
  }
  &:hover{
  img{
    opacity: 1;
  }

}
}
